/* Toast Image Editor */
.tui-image-editor-controls {
  background: #f23357 !important;
}
.tui-image-editor-menu use.normal.use-default,
.tui-image-editor-controls .tui-image-editor-help-menu use.normal.use-default {
  fill-rule: evenodd;
  fill: #fff !important;
  stroke: #fff !important;
}
.tui-image-editor-menu use.active.use-default,
.tui-image-editor-help-menu use.active.use-default {
  fill-rule: evenodd;
  fill: #f23357 !important;
  stroke: #f23357 !important;
}
.tui-image-editor-submenu use.active.use-default,
.tui-image-editor-submenu use.normal.use-default {
  fill-rule: evenodd;
  fill: #000;
  stroke: #000;
}

/* .tui-image-editor-submenu .tui-image-editor-menu-filter,
.tui-image-editor-menu .tie-btn-filter, */
.tui-image-editor-menu .tie-btn-flip,
.tui-image-editor-menu .tie-btn-shape,
.tui-image-editor-menu .tie-btn-icon,
.tui-image-editor-menu .tie-btn-draw {
  display: none !important;
}
.tie-btn-mask .svg_ic-menu use {
  display: none !important;
}

.tie-btn-mask .svg_ic-menu {
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,) !important;
}
.tie-btn-mask.active .svg_ic-menu {
  background-image: url(data:image/png;base64,) !important;
}

.tui-image-editor-container .tui-image-editor-menu .tie-btn-mask:hover:after {
  content: "Image" !important;
}

.tui-image-editor-header,
.tie-btn-hand {
  display: none;
}

.tui-image-editor-submenu
  .tui-image-editor-menu-mask
  li:first-child
  .tui-image-editor-button
  label:after {
  content: "Upload Image";
  color: #fff;
  opacity: 1 !important;
  background: #292929;
  position: absolute;
  left: 0;
  right: 0;
}
